import "./styles.css";
import React, { Component } from "react";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hour: 0,
      minute: 0,
      second: 0,
      value: [],
      array: null
    };
    console.log("Constructor");
  }
  timerIdSeconds() {
    this.timer1 = setInterval(() => {
      this.setState({ second: this.state.second + 1 });
    }, 1000);
  }
  componentDidUpdate() {
    console.log("componentDidUpdate");
    if (
      this.state.second === 0 &&
      this.state.minute === 0 &&
      this.state.hour === 0
    ) {
      this.timerIdSeconds();
    }
    if (this.state.second === 60) {
      this.setState({ second: 0 });
      this.setState({ minute: this.state.minute + 1 });
    }
    if (this.state.minute === 60) {
      this.setState({ minute: 0 });
      this.setState({ hour: this.state.hour + 1 });
    }
  }
  componentDidMount() {
    this.timerIdSeconds();
    console.log("componentDid Mount");
  }
  render() {
    return (
      <div className="App">
        <h1>
          {this.state.hour + ":" + this.state.minute + ":" + this.state.second}
        </h1>
        <button
          onClick={() => {
            this.setState({ second: 0, minute: 0, hour: 0 });
          }}
        >
          Start
        </button>
        <button
          onClick={() => {
            clearInterval(this.timer1);
            this.setState({
              value: [
                ...this.state.value,
                this.state.hour +
                  ":" +
                  this.state.minute +
                  ":" +
                  this.state.second +
                  "  "
              ]
            });
            /* this.setState({
              array: this.state.value.map((element) => {
                return (document.createElement("h1").innerHtml = element);
              })
            })*/
          }}
        >
          Stop
        </button>
        <button
          onClick={() => {
            this.setState({ value: [] });
          }}
        >
          Clear All
        </button>

        <h1>{this.state.value}</h1>
        {console.log("render")}
      </div>
    );
  }
}
